import React from 'react';

import { Button, Form, FormControl, Container, Row, Col } from 'react-bootstrap';

import './Header.css';
import logo from "./../assets/img/logo.png";


function Header() {
    return (
        <div>
            <div className="Topbar">
                <Container fluid>
                <Row>
                    <Col>
                        The best online shopping website
                    </Col>
                    <Col>
                        <a href="/"><i class="material-icons">call</i> 01712-121245</a>
                    </Col>
                    <Col>
                        <a href="/"><i class="material-icons">room</i> Our Location</a>
                        <a href="/">EN</a> | <a href="/">বাং</a>
                    </Col>
                </Row>
                </Container>
            </div>
            <div className="Header">
                <Container>
                    <Row>
                        <Col md={4} className="logo">
                            <img
                                src={logo}
                                width="250px"
                                height="95px"
                                className="d-inline-block align-top"
                                alt="React Bootstrap logo"
                            />
                        </Col>
                        <Col xs={8}>
                            <Row className="search-shopping">
                                <Col md={8} className="search-section">
                                    <Form inline>
                                        <FormControl type="text" placeholder="Search" className="search-box" />
                                        <Button variant="outline-light" className="search-button">
                                            <i class="material-icons">search</i>
                                        </Button>
                                    </Form>
                                </Col>
                                <Col className="shopping-section">
                                    <Button variant="light">
                                        <i class="material-icons animated infinite fadeInLeft slow">shopping_cart</i> 0.00 tk
                                    </Button>
                                    <Button variant="light">
                                        <i class="material-icons animated infinite pulse">account_circle</i> Login
                                    </Button>
                                </Col>
                            </Row>
                        </Col>
                    </Row>
                </Container>
            </div>
        </div>
    );
}

export default Header;