import React from 'react';

import { Container, Row, Col } from 'react-bootstrap';

import './why-apedin.css';


function WhyApedin() {
    return (
        <div className="why-apedin">
            <Container className="why-apedin-main">
                <Row>
                    <Col md={1} className="drawer-icon">
                        <i class="material-icons">menu</i>
                    </Col>
                    <Col className="why-section pt-3">
                        <Row>
                            <Col>
                                <h4>
                                    Why Apedin
                                    <i class="material-icons why-arrow">double_arrow</i>
                                </h4>
                            </Col>
                            <Col>
                                <span><i class="material-icons">local_shipping</i></span>
                                <p>FREE DELIVERY</p>
                            </Col>
                            <Col>
                                <span><i class="material-icons">restore</i></span>
                                <p>7-DAYS RETURN</p>
                            </Col>
                            <Col>
                                <span><i class="material-icons">beenhere</i></span>
                                <p>GUARANTEE</p>
                            </Col>
                            <Col>
                                <span><i class="material-icons">attach_money</i></span>
                                <p>PASSIVE INCOME</p>
                            </Col>
                        </Row>
                    </Col>
                </Row>
            </Container>
        </div>
    );
}

export default WhyApedin;